import { on, nodeListToArray } from "./helpers";
import { disableBodyScroll, enableBodyScroll } from "body-scroll-lock";

const navbarBtn = document.getElementById("navHamburger");
const header = document.getElementsByTagName("header")[0];

function scrollTo(hash) {
    if (document.querySelector(hash)) {
        document.querySelector(hash).scrollIntoView({
            behavior: "smooth"
        });
    }
}

function classToggle() {
    navbarBtn.classList.toggle("is-active");
    header.classList.toggle("header-open");
    if (header.classList.contains("header-open")) {
        disableBodyScroll(header);
    } else {
        enableBodyScroll(header);
    }
}

on(navbarBtn, "click", classToggle);
